import line from '../assets/img/hero/text-background.jpg'

export const Hero = () => {
    return (
        <section className="main-hero">
            <div className="container backgrounder">
                <div className="flex align-center wrap full-height">
                    <h2>Find the best <span>talent</span></h2>
                    <div className="flex column space-between">
                        <img src={line} alt="" />
                        <p>
                            Finding the right people and building high performing teams can be hard.
                            Most companies aren't tapping into the abundance of global talent.
                            We're about to change that.
                        </p>
                    </div>
                </div>
            </div>
        </section>
    )
}