import { useState } from 'react'
import logo from '../assets/img/app-header/logo.png'

export const AppHeader = () => {
    const [isNavOpen, navToggler] = useState(false)

    const onToggleNav = () => {
        navToggler(!isNavOpen)
    }


    return (
        <nav className="main-nav">
            <div className="container flex align-center space-between">
                <img src={logo} alt="brand logo" />

                <span className="pointer fas hamburger-icn" onClick={onToggleNav}></span>

                <div className={"links-container " + (isNavOpen ? 'open-nav' : 'closed-nav')}>
                    <div className="pointer fas close-icn" onClick={onToggleNav}></div>
                    <ul className="clean-list list-container">
                        <li className="pointer">home</li>
                        <li className="pointer">about</li>
                        <li className="pointer">contact us</li>
                    </ul>
                </div>
            </div>
        </nav>
    )
}