import logo from '../assets/img/app-header/logo.png'

export const Footer = () => {
    return (
        <footer className="main-footer">
            <div className="container flex space-between">
                <div className="flex space-between">
                    <div className="flex column space-between inside-links-container">
                        <img src={logo} alt="brand logo" />
                        <div className="flex space-between">
                            <span className="inside-links">home</span>
                            <span className="inside-links">about</span>
                        </div>
                    </div>
                    <div className="flex column space-between contact-container">
                        <div>987 Hillcrest Lane</div>
                        <div>Irvine, CA</div>
                        <div>California 92714</div>
                        <div>Call Us: 949-833-7432</div>
                    </div>
                </div>

                <div className="flex column space-between">
                    <div className="flex social-networks-container">
                        <span className="fab facebook-icn"></span>
                        <span className="fab pinterest-icn"></span>
                        <span className="fab twiter-icn"></span>
                    </div>
                    <div className="copyrights">
                        Copyright 2020. All Rights Reserved.
                    </div>
                </div>
            </div>
        </footer>
    )
}